.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background: linear-gradient(45deg, #00c6fb, #005bea);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
  &-image {
    width: 100%;
    overflow: hidden;
    height: 35rem;
    z-index: 0;
    img {
      width: 115%;
      height: 100%;
      object-fit: cover;
      transform: rotateZ(-5deg) translateY(-4rem) translateX(-2rem);
    }
  }
}
